/**
 * asciibase64IE10+
 */
export function utf8ToBase64 (str) {
  return window.btoa(unescape(encodeURIComponent(str)))
}

/**
 * base64utf8
 */
export function base64ToUtf8 (str) {
  return decodeURIComponent(escape(window.atob(str)))
}

/**
 * Action Creators 
 * @param type Action
 * @param argNames 
 * @return {Function}
 */
export function makeActionCreator (type, ...argNames) {
  return function (...args) {
    let action = { type }
    argNames.forEach((arg, index) => {
      action[argNames[index]] = args[index]
    })
    return action
  }
}

/**
 * Reducers  reducers  action types  handlers 
 * @param initialState 
 * @param handlers reducer{ actionType: handler }
 * @return {Function}
 */
export function createReducer (initialState, handlers) {
  return function reducer(state = initialState, action) {
    if (handlers.hasOwnProperty(action.type)) {
      return handlers[action.type](state, action)
    } else {
      return state
    }
  }
}

/**
 * store#state
 * @param oldObject state
 * @param newValues  { completed: true }
 * @return {Object} state
 */
export function updateObject (oldObject, newValues) {
  //  Object.assign
  return Object.assign({}, oldObject, newValues)
}

/**
 * store#state
 * @param array state
 * @param itemId ID
 * @param updateItemCallback  item => updateObject(item, { completed: !item.completed })
 * @return {Array} state
 */
export function updateItemInArray (array, itemId, updateItemCallback) {
  const updatedItems = array.map(item => {
    if(item.id !== itemId) { // 
      return item
    }

    // 
    const updatedItem = updateItemCallback(item)
    return updatedItem
  })

  return updatedItems
}



